import React, { useState } from "react";

import UserForm from "./User/UserForm";
import UsersList from "./Users/UsersList";

const exampleUsersData = [
  { id: "u1", name: "Anna", age: 38 },
  { id: "u2", name: "Marcin", age: 37 },
];

function App() {
  const [usersData, setUsersData] = useState(exampleUsersData);

  const addUserDataHandler = (enteredUserData) => {
    const userData = {
      ...enteredUserData,
      id: Math.random().toString(),
    };

    setUsersData((prevUsersData) => {
      return [userData, ...prevUsersData];
    });
  };

  return (
    <div>
      <div>
        <UserForm onAddUserData={addUserDataHandler} />
        {usersData.map((usersData) => (
          <UsersList
            key={usersData.id}
            name={usersData.name}
            age={usersData.age}
          />
        ))}
      </div>
    </div>
  );
}

export default App;
