import React, { useState } from "react";

import styles1 from "./AddUser.module.css";
import styles2 from "./UserForm.module.css";
import ErrorModal from "./ErrorModal";

function UserForm(props) {
  const [enteredName, setEnteredName] = useState("");
  const [enteredAge, setEnteredAge] = useState("");
  const [displayedErrorModal, setDisplayedErrorModal] = useState("");

  const nameChangeHandler = (e) => {
    setEnteredName(e.target.value);
  };

  const ageChangeHandler = (e) => {
    setEnteredAge(e.target.value);
  };

  const submitHandler = (e) => {
    e.preventDefault();

    const userData = {
      name: enteredName,
      age: enteredAge,
    };

    const errorModal = <ErrorModal />; 

    if (enteredName.trim().length === 0 || enteredAge.trim().length === 0) {
      setDisplayedErrorModal(errorModal);
    } else {
      props.onAddUserData(userData);
      setEnteredName("");
      setEnteredAge("");
    }
  };

  return (
    <form className={styles2.input} onSubmit={submitHandler}>
      <label>Username</label>
      <input type="text" value={enteredName} onChange={nameChangeHandler} />
      <label>Age (years old)</label>
      <input type="number" value={enteredAge} onChange={ageChangeHandler} />
      <button type="submit" className={styles1.button}>
        Add User
      </button>
      {displayedErrorModal}
    </form>
  );
}

export default UserForm;